.newNote-form {
  display: flex;
  gap: 15px;
  align-items: center;
  margin-bottom: 1rem;
}
.input-newNote {
  padding: 0.5rem;
  font-size: 1.1rem;
  transition: border-color 0.5s ease-out;
  border-radius: 5px;
  padding-left: 50px;
  background-color: 'lightgray';
}
.btn-newNote {
  width: 100px;
  padding: 5px;
  font-size: 20px;
}
